<template>
	<div class="hello">
		<h1>{{ msg }}</h1>
		<a href="#" @click.prevent="signout">登出</a>
	</div>
</template>

<script>
export default {
	name: 'HelloWorld',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App'
		}
	},
	methods:{
		signout(){
			const api = `${process.env.APISERVER}/logout`;
			var vm = this;
			// 登出API是使用 post方法，不用傳參數
			this.$http.post(api).then(response => {
				console.log(response.data);
				if (response.data.success){
					// 登出成功則會轉跳到登入頁面
					vm.$router.push("/login")
				}
			});
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
	font-weight: normal;
}
ul {
	list-style-type: none;
	padding: 0;
}
li {
	display: inline-block;
	margin: 0 10px;
}
a {
	color: #42b983;
}
</style>



